import React, { useState, useEffect, useMemo } from "react";

const Characters = ({favourites, setFavourites}) => {
  const [characters, setCharacters] = useState([]);

  const getCharacters = async () => {
    const response = await fetch("https://rickandmortyapi.com/api/character");
    const data = await response.json();
    setCharacters(data.results);
  };

  const [searchValue, setSearchValue] = React.useState('');
  const searchRef = React.useRef(null);

  function addFavourite(character){
    if(favourites.includes(character))
      return;
    
    const newFavourites = [...favourites];
    newFavourites.push(character);
    setFavourites(newFavourites);
    console.log(favourites);
  }

  useEffect(() => {
    getCharacters();
  }, []);

  const searchCharacters = useMemo(() => characters.filter((character) => {
    return character.name.toLowerCase().includes(searchValue.toLowerCase());
  }),
  [characters, searchValue]);

  return (
    <React.Fragment>
      <div className="characters-header">
        <h1>Characters</h1>
        <input placeholder="Search..." type="text" ref={searchRef} value={searchValue} onChange={() => {setSearchValue(searchRef.current.value)}} />
      </div>

      <div className="Characters">

        {searchCharacters.map((character) => (
            <div onClick={() => addFavourite(character)}>
              <img alt={character.name} width="200px" src={character.image}/>
              <h2 key={character.id}>{character.name}</h2>
            </div>
        ))}
      </div>
    </React.Fragment>
    
  );
};

export default Characters;