import React from "react";

const Favourites = ({favourites, setFavourites}) => {

  function deleteFavourite(character){
    const newFavourites = [...favourites];
    newFavourites.splice(newFavourites.findIndex(x => x.id === character.id), 1);
    setFavourites(newFavourites);

    console.log(newFavourites);
  }

  return (
    <React.Fragment>
      {favourites.length > 0 &&
      (<div className="favourites-header">
         <h1>Favourites</h1>
       </div>)}

      
      <div className="Favourites">

        {favourites.map((character) => (
            <div onClick={() => {deleteFavourite(character)}}>
              <img alt={character.name} width="200px" src={character.image}/>
              <h2 key={character.id}>{character.name}</h2>
            </div>
        ))}
      </div>
    </React.Fragment>
    
  );
};

export default Favourites;